@media screen and (max-width: 820px) {
  @import "colors";

  .cart {
    grid-template-columns: 1fr;

    > aside {
      background-color: $primary;
      color: $tertiary;
      height: 100%;
      width: 100%;
      box-shadow: -10px 0px 10px rgba(0, 0, 0, 0.3);
      display: flex;
      justify-content: center;
      flex-direction: column;
      padding: 2rem;
      > h2 {
        margin: 1rem;
        font: 100 1.2rem "Roboto";
        letter-spacing: 2px;
      }
    }
  }
}
